import {
	Divider,
	HeaderPage,
	ResultApproach,
	ComparableProperty,
	ComparationAnalysis,
	HomogolationAnalysis,
	ApproachForm,
} from "Components/Appraisal";

import { productSetOfValues } from "Helpers/formulas";

const BusinessApproach = ({
	delegations : {
		onSubmit,
		hookForm,
		isLoading,
		ResultRef,
		comparablePropertyRef,
		comparationAnalysisRef,
		homogolationAnalysisRef,
	},
}) => {

	const { isDirty } = hookForm.formState;

	const sections =[
		{
			title : "Información de Mercado",
			ref   : comparablePropertyRef,
		},
		{
			ref   : homogolationAnalysisRef,
			title : "Homologación CUS/FACTORES",
		},
		{
			ref   : comparationAnalysisRef,
			title : "Analisis del Enfoque de Comparación",
		},
		{
			ref   : ResultRef,
			title : "Resultado",
		},
	];
	return (
		<div className="section-table">
			<HeaderPage
				isDirty={isDirty}
				currentIndex={[2, 1]}
				section="body"
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>
			<ApproachForm
				id="approach-business"
				hookForm={hookForm}
				sections={sections}
				onSubmit={onSubmit}
				sectionTitle="Enfoque de Mercado"
			>
				<Divider innerRef={comparablePropertyRef} title="Información de Mercado" />
				<ComparableProperty />
				<Divider innerRef={homogolationAnalysisRef} title="Homologación CUS/FACTORES" />
				<HomogolationAnalysis />
				<Divider innerRef={comparationAnalysisRef} title="Analisis del Enfoque de Comparación" />
				<ComparationAnalysis />
				<Divider innerRef={ResultRef} title="Resultado" />
				<ResultApproach
					name="bussines-result"
					fromula={productSetOfValues}
					title="Resultado del Enfoque de Comparación:"
					deps={["unit_amount_value", "salable_value"]}
					tooltip="Resultado del Enfoque de Comparacion = (Monto unitario Aplicable) x (Superficie Vendible)"
				/>
			</ApproachForm>
		</div>
	);
};

export default BusinessApproach;
