import { useRef }    from "react";
import { useForm }   from "react-hook-form";
import { useParams } from "react-router-dom";

import { getDefaultArrVals }       from "Helpers";
import { useHandlerMutation }      from "Helpers/Hooks";
import { AppraisalHOCPageFetcher } from "Components/Appraisal";

import {
	CUS,
	PARAMS,
	DEFAULT_FETCH_DATA,
	COMPARABLE_PROPERTY,
	HOMOLOGATION_FACTORS,
} from "./BusinessApproach.constants";

import BusinessApproach       from "./BusinessApproach";
import { productSetOfValues } from "Helpers/formulas";

const BusinessApproachContainer = ({ appraise = DEFAULT_FETCH_DATA }) => {

	const { id : appraiseId } = useParams();

	const comparablePropertyRef   = useRef(null);
	const homogolationAnalysisRef = useRef(null);
	const comparationAnalysisRef  = useRef(null);
	const ResultRef               = useRef(null);

	// ------------> RTK-Q <---------------
	const [ editApprasial, { isLoading  : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });

	const { business_approach, page_four } = appraise;
	const {
		cus,
		justifications,
		comparable_property,
		homologation_factors,
	} = business_approach;

	const defaultValues = {
		...business_approach,
		comparable_property  : getDefaultArrVals(comparable_property, COMPARABLE_PROPERTY),
		cus                  : getDefaultArrVals(cus, CUS),
		homologation_factors : getDefaultArrVals(homologation_factors, HOMOLOGATION_FACTORS),
		justifications       : [...Array(6).keys()].map((i) => ({ value : justifications?.[i] ?? "" })),
		salable_value        : page_four.surface.superficie_salable,
	};

	const hookForm = useForm({ defaultValues });

	const { handleSubmit } = hookForm;


	const buildDataToSubmit = (formData) => {
		const justifications = formData.justifications.map(({ value }) => value);
		const dataToSend = {
			...formData,
			justifications,
			business_result : getBussinesApproachResult(formData),
		};
		return dataToSend;
	};

	const getBussinesApproachResult = (formData) => {
		const { unit_amount_value, salable_value } = formData;
		const result = productSetOfValues([unit_amount_value, salable_value]);
		return result;
	};

	const onSubmit =  handleSubmit(async (formData) => {
		const dataToSubmit = buildDataToSubmit(formData);

		await editApprasial({
			module : `/appraisals/business-approach/${appraiseId}`,
			method : "PUT",
			data   : dataToSubmit,
			tags   : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<BusinessApproach
			delegations={{
				onSubmit,
				hookForm,
				ResultRef,
				comparablePropertyRef,
				comparationAnalysisRef,
				homogolationAnalysisRef,
				isLoading : isLoadingMutation,
			}}
		/>
	);
};

export default AppraisalHOCPageFetcher(BusinessApproachContainer, PARAMS);
